const Header = props => (
  <div>
    {props.message !== null && <h1>Your booking has succeeded</h1>}
    <pre>{JSON.stringify(props.message)}</pre>
    <style jsx>{`
      pre, h1 {
        font-family: "Arial";
        padding: 20;
        color: #39f;
      }
    `}</style>
  </div>
);

export default Header;
